<template>
  <main>
    <div class="wrapper shop flex-wrap">
      <div class="col col-lg-12 col-12 shop-header">
        <h2><span>P</span>roducts</h2>
        <input type="text" v-model="keyword" name="tbSearch" id="tbSearch" placeholder="Search for name.."/>
      </div>
      <div class="col col-lg-12 col-12">
        <ProductsSection @addProductToCart="callFunctionAddToCartFromStore" :products="getProductsWithOffset"/>
      </div>
      <div class="pages" v-if="getProducts.length">
        <button v-for="page, ind in totalNumberOfPages" :key="ind" 
        :class="currentPage == page -1 ? 'active' : ''" @click="changeCurrentPage(page)" >{{page}}</button>
      </div>
      <h4 class="empty" v-if="!getProducts.length">There is no products for your search</h4>
    </div>
  </main>
</template>
<script>
import {mapGetters , mapActions , mapMutations } from 'vuex'
import ProductsSection from './partials/shop/ProductsSection.vue'


export default {
  name: 'ShopComp',
  data(){
    return{
      keyword: '',
      currentPage : 0,
      limit : 9
    }
  },
  computed:{
    ...mapGetters(['getProducts' , 'getProductsFromCart']),
    totalNumberOfPages(){
      let numberOfPages = Math.ceil(this.getProducts.length / this.limit);
      return numberOfPages
    },
    getProductsWithOffset(){
      let products = this.getProducts
      let pos = this.currentPage * this.limit

      products = products.slice(pos , this.limit + pos)

      return products
    }
  },
  methods:{
    ...mapActions(['loadProductsFromApi' , 'addItemToCart']),
    ...mapMutations(['changeKeyword']),
    loadProducts(){
      this.loadProductsFromApi().then(resp=>{
        localStorage.setItem("products", JSON.stringify(this.getProducts))
      }).catch(err=>{ 

      });
    },
    callFunctionAddToCartFromStore(id){
      this.$store.dispatch('addItemToCart', id).then(resp=>{
        this.$emit('showPopup' , resp)
      })
    },
    changeCurrentPage(page){
      this.currentPage = page - 1
    }
  },
  mounted(){
    this.loadProducts()
  },
  components:{
    ProductsSection
  },
  watch:{
    keyword(){
      this.$store.commit('changeKeyword' ,  this.keyword);
      this.currentPage =0
      this.loadProducts()
    }
  }
}
</script>