<template>
  <div v-if="visible" class="popup">
    <div class="content">
      <button @click="closePopup" class="closePopup"><i class="zmdi zmdi-close"></i></button>
      <h3>{{ message }}</h3>
    </div>
  </div>
</template>
<script>
export default {
  name: "PopupComp",
  props:{
    visible:{
      type: Boolean,
      required: true,
    },
    message:{
      type: String,
      required: true,
    }
  },
  methods:{
    closePopup(){
      this.$emit('changePopupVisibility' , false)
    }
  },

}
</script>