<template>
  <tr>
    <td>{{ind + 1}}</td>
    <td><img :src="makeImageSrc(product.image)" :alt="product.name"/></td>
    <td>{{product.name}}</td>
    <td v-if="!product.discaunt">${{product.price}}</td>
    <td v-if="product.discaunt">${{getCurrentPrice(product)}}</td>
    <td>
      <select @change="changeProductQuantity($event)" :name="makeUniqueDDL(product.id)" :id="makeUniqueDDL(product.id)">
        <option :selected="checkIfThisIsQuantityOfProduct(product.quantity , option.value)" v-for="option , ind  in options " :key="ind" :value="option.value">{{ option.value }}</option>
      </select>
    </td>
    <td><button @click="removeItemFromCart"  class="removeFromCart"><i class="fas fa-trash"></i></button></td>
  </tr>
</template>
<script>
export default {
  name: 'CartItemComp',
  props:{
    product:{
      type: Object,
      required: true,
    },
    ind:{
      type:Number,
      required: true,
    }
  },
  data(){
    return{
      options: [],
    }
  },
  mounted(){
    for (let i = 1; i < 21; i++) {
      this.options.push({value: i});
    }
  },
  methods:{
    makeImageSrc(src){
      if(this.product.image.length > 25){
        return this.product.image
      }
      return require('@/assets/img/shop/' + this.product.image + '.png')
    },
    makeUniqueDDL(id){
      return 'ddlQuantity' + id
    },
    checkIfThisIsQuantityOfProduct(quantity , option){
      if(quantity == option)return "selected"
      return false
    },
    getCurrentPrice(product){
      let currentPrice =  product.price * (1 -product.discaunt / 100)
      if(Number.isInteger(currentPrice)){
        return currentPrice;
      }
      return currentPrice.toFixed(2);
    },
    changeProductQuantity(event){
      let selectVal = event.target.value
      this.$emit("changeProductQuantity", selectVal , this.product.id )
    },
    removeItemFromCart(){
      this.$emit("removeItemFromCart", this.product.id)
    }
  }
}
</script>