<template>
  <div class="field">
    <div class="input-field">      
      <input @blur="getDownLabelIfInputIsEmpty" @focus="getUpLabel" :type="type" v-model="localValue" :name="name" :id="name"/>
      <label v-if="update" class="goUp" :for="name">{{ label }}</label>
      <label v-if="!update" :for="name">{{ label }}</label>
    </div>
    <span v-if="hint" class="hint">{{ hint }}</span>
    <span v-if="error" class="err">{{ error }}</span>
  </div>
</template>
<script>
export default {
  name:"TextFieldComp",
  data(){
    return{
      localValue : ''
    }
  },
  props:{
    name:{
      type:String,
      required:true,
    },
    label:{
      type:String,
      required:true,
    },
    hint:{
      type:String,
      required:false,
    },
    error:{
      type:String,
      required:false,
    },
    type:{
      type:String,
      required:true,
    },
    value:{
      type:[String , Number ,null],
      required:true,
    },
    update:{
      type:Boolean,
      required:false,
    },
  },
  mounted(){
    this.localValue =  this.value

  },
  watch:{
    localValue(){
      this.$emit('input' , this.localValue);
    },
    value(){
      this.localValue = this.value
    }
  },
  methods:{
    getDownLabelIfInputIsEmpty(event){
      let input = event.target;
      let label = input.parentElement.querySelector('label');
      if(input.value.length > 0){
        label.classList.add('goUp');
        return;
      }
      label.classList.remove('goUp'); 
    },
    getUpLabel(e){
      let input = e.target;
      let label = input.parentElement.querySelector('label');
      label.classList.add('goUp');
    }
  }
}
</script>