<template>
  <tr>
    <td>{{ ind + 1 }}</td>
    <td><img :src="getImageUrl" :alt="product.name"></td>
    <td>{{ product.manufacturer }}</td>
    <td>{{ product.model }}</td>
    <td v-if="!product.discaunt">${{ product.price }}</td>
    <td v-if="product.discaunt">${{ Number.isInteger(getCurrentPrice) ? getCurrentPrice : getCurrentPrice.toFixed(2)  }}</td>
    <td><button @click="goToUpdatePage(product.id)" class="editBtn"><i class="zmdi zmdi-edit"></i></button></td>
    <td><button @click="removeProduct(product.id)" class="removeBtn"><i class="zmdi zmdi-minus-square"></i></button></td>
  </tr>
</template>
<script>
import {mapActions} from 'vuex'

export default {
  name: 'ProductsInAdminPageComp',
  props:{
    product:{
      type:Object,
      required:true,
    },
    ind:{
      type:Number,
      required:true,
    }
  },
  computed:{
    getImageUrl(){
      if(this.product.image.length > 25){
        return this.product.image
      }
      return require('@/assets/img/shop/' + this.product.image + '.png')
    },
    getCurrentPrice(){
      return this.product.price * (1 - this.product.discaunt / 100)
    }
  },
  methods:{
    ...mapActions(['removeProductsInApi']),
    removeProduct(id){
      this.$emit('removeProduct', id)
    },
    goToUpdatePage(id){
      this.$router.push('/insertUpdate/' + id )
    }
  }
}
</script>