<template>
  <main>
    <div class="seeMore wrapper">
      <div class="col col-lg-6 col-md-6 col-sm-12 col-12">
        <img :src="makeImageSrc" :alt="product.name"/>
      </div>
      <div class="col col-lg-6 col-md-6 col-sm-12 col-12">
        <table class="tableLayout">
          <thead>
            <tr>
              <th>Attribute</th>
              <th>Value</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Name</td>
              <td>{{ product.name }}</td>
            </tr>
            <tr>
              <td>Manufacturer</td>
              <td>{{ product.manufacturer }}</td>
            </tr>
            <tr>
              <td>Model</td>
              <td>{{ product.model }}</td>
            </tr>
            <tr v-if="!product.discaunt">
              <td>Price</td>
              <td>${{ product.price }}</td>
            </tr>
            <tr v-if="product.discaunt">
              <td>Old Price</td>
              <td><del>${{ product.price }}</del></td>
            </tr>
            <tr v-if="product.discaunt">
              <td>New Price</td>
              <td>${{ getNewPriceWithDiscaunt }}</td>
            </tr>
            <tr v-if="product.discaunt">
              <td>Discaunt</td>
              <td>{{ product.discaunt }}%</td>
            </tr>
            <tr v-for="val , key in product.specification" :key="key">
              <td>{{ key }}</td>
              <td>{{ val }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </main>
</template>
<script>
import { mapActions  } from 'vuex'

export default {
  name:"SeeMoreComp",
  data(){
    return{
      product: {}
    }
  },
  mounted(){
    let productId = this.$route.params.id;

    this.loadProductWithSomeId(productId)
  },
  methods:{
    ...mapActions(['loadProductWithSomeId']),
    loadProductWithSomeId(id){
      let that = this;
      this.$store.dispatch('loadProductWithSomeId' , id).then(resp=>{
        this.product = resp.data
        }).catch(err=>{
          that.$router.push('/shop');
        })
    }
  },
  computed:{
    getNewPriceWithDiscaunt(){
      return parseInt(this.product.price * (1 - this.product.discaunt / 100));
    },
    makeImageSrc(){
      if(this.product.image){
        if(this.product.image.length > 25){
          return this.product.image
        }
        return require('@/assets/img/shop/' + this.product.image + '.png')
      }
    },
    isOnDiscaunt(){
      return this.product.discaunt? 'onDiscaunt': '';
    }
  }
}
</script>