<template>
  <nav>
    <ul v-if="router">
      <li v-for="item,ind in navItems" :key="ind" v-role="item.role"> 
        <router-link :to="item.path" >{{ item.text }}</router-link>
      </li>
    </ul>
    <ul v-if="!router">
      <li v-for="item,ind in navItems" :key="ind">
        <a v-if="item.blank" target="_blank" :href="item.path">{{ item.text }}</a>
        <a v-if="!item.blank" :href="item.path">{{ item.text }}</a>
      </li>
    </ul>
  </nav>
</template>
<script>
export default {
  name:"NavComp",
  props:{
    navItems: {
      type:Array,
      required: true
    },
    router:{
      type: Boolean,
      require:true
    }
  }
}
</script>