export default ({
  state:{
    users: [],
    loggedUser: {role: 'unauthorized', username: 'guest'}
  },
  mutations:{
    addUser(state ,user){
      state.users.push(user);
    },
    addUsers(state ,users){
      state.users = users;
    },
    changeLoggedUser(state, user){
      if(!user){
        state.loggedUser.role = 'unauthorized'
        state.loggedUser.username= 'guest'
        localStorage.removeItem('user')
        return;
      }
      state.loggedUser.role = user.role
      state.loggedUser.username = user.username
    },
    addProductsToLoggedUserOrders(state, payload){
      let products = payload.products;

      let userInStore = state.users.find(u=> u.username === state.loggedUser.username);

      if(userInStore){
        let date = new Date();
        let dateString = `${date.getDate()}/${date.getMonth()}/${date.getFullYear()}` 
        userInStore.orders.push({
          products: products,
          date: dateString
        })
  
        localStorage.setItem('users' , JSON.stringify(state.users))
      }
    },
  },
  actions:{
    registerUser({commit , state} , user){
      return new Promise(function(resolve, reject){

        let existedUser = state.users.find(u=> u.username === user.username || u.email === user.email);
        if(existedUser){
          reject('There is already a user with that username or email')
          return;
        }
        commit('addUser' , user);
        localStorage.setItem('users' , JSON.stringify(state.users))
        resolve('Your account has been created')
      })
    },
    checkUserParams({ commit , state} , user){

      return new Promise(function(resolve, reject){
        let userInStore = state.users.find(u=> u.email === user.email && u.password === user.password)
        if(userInStore){
          commit('changeLoggedUser' , userInStore)
          resolve(userInStore)
        }else{
          reject("There is no user with that credentials")
        }
      })
    }
  },
  getters:{
    role(state){
      return state.loggedUser.role
    },
    users(state) {
      return state.users
    },
    getLoggedUser(state){
      let user = state.users.find(u=> u.username === state.loggedUser.username)
      if(!user) return {}
      return user
    },
    getUserOrders(state){
      let user = state.users.find(u=> u.username === state.loggedUser.username)
      if(user){
        return user.orders
      }
    }

  }

})