import axios from "axios";

export default {
  state: {
    keyword: "",
    products: [],
    productsInCart: [],
    totalQuantity: 0,
    totalPrice: 0,
    deleteMess: "",
  },
  getters: {
    getProducts(state) {
      return state.products;
    },
    getProductsFromCart(state) {
      if (state.productsInCart && state.productsInCart.length)
        return state.productsInCart;
      return [];
    },
    getProductById: (state) => (id) => {
      return state.products.find((product) => product.id === id);
    },
    getTotalPriceForItemsInCart(state) {
      return state.totalPrice;
    },
    getTotalQuantityForItemsInCart(state) {
      return state.totalQuantity;
    },
    getDeleteMessage(state) {
      return state.deleteMess;
    },
  },
  mutations: {
    addProduct(state, product) {
      state.products.push(product);
    },
    changeProductsInCart(state, products) {
      state.productsInCart = products;
    },
    setProducts(state, response) {
      state.products = response;
    },
    setProductsInCart(state, response) {
      state.productsInCart = response;
    },
    changeKeyword(state, newKeyword) {
      state.keyword = newKeyword;
    },
    changeProductQuantity(state, data) {
      let product = state.productsInCart.find((p) => p.id == data.id);
      if (product) {
        product.quantity = data.sel;
        localStorage.setItem(
          "itemsInCart",
          JSON.stringify(state.productsInCart)
        );
      }
    },
    updateTotalQuantityAndPrice(state) {
      this.commit("updateTotalQuantity");
      this.commit("updateTotalPrice");
    },
    updateTotalQuantity(state) {
      state.totalQuantity = 0;

      state.productsInCart.forEach((p) => {
        state.totalQuantity += +p.quantity;
      });
    },
    updateTotalPrice(state) {
      state.totalPrice = 0;

      state.productsInCart.forEach((p) => {
        if (p.discaunt) {
          let currentPrice = p.price * (1 - p.discaunt / 100);
          state.totalPrice += currentPrice * p.quantity;
        } else {
          state.totalPrice += p.price * p.quantity;
        }
      });
    },
    addItemToProductsInCart(state, product) {
      state.productsInCart.push(product);
    },
    removeItemFromCart(state, id) {
      state.productsInCart = state.productsInCart.filter((p) => p.id !== id);

      localStorage.setItem("itemsInCart", JSON.stringify(state.productsInCart));
    },
    removeProductFromStore(state, id) {
      state.products = state.products.filter((p) => p.id !== id);
      this.commit("removeItemFromCart", id);
    },
  },
  actions: {
    loadProductsFromApi({ commit, state }) {
      let params = {};
      if (state.keyword !== "") {
        params.name = state.keyword;
      }
      return axios
        .get("https://64839515f2e76ae1b95cad57.mockapi.io/products", { params })
        .then((resp) => {
          commit("setProducts", resp.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    loadProductWithSomeId({ commit, state }, id) {
      return axios.get(
        "https://64839515f2e76ae1b95cad57.mockapi.io/products/" + id
      );
    },
    loadCartProducts({ commit }, cartProducts) {
      commit("setProductsInCart", cartProducts);
    },
    getProductWithSomeId({ state }, id) {
      return state.products.find((p) => p.id === id);
    },
    addItemToCart({ commit, state }, id) {
      let itemsInCart = state.productsInCart;

      let product = state.products.find((p) => p.id == id);
      product.quantity = 1;
      let message = "";

      if (itemsInCart && itemsInCart.length) {
        if (itemsInCart.find((item) => item.id == id)) {
          message = "Product is already in the cart";
          return message;
        }
      }
      commit("addItemToProductsInCart", product);
      localStorage.setItem("itemsInCart", JSON.stringify(state.productsInCart));

      message = "Product is added to the cart";
      return message;
    },
    removeProductsInApi({ commit, state }, id) {
      return axios.delete(
        "https://64839515f2e76ae1b95cad57.mockapi.io/products/" + id
      );
    },
    createNewProductInApi({ commit, state }, product) {
      return axios.post(
        `https://64839515f2e76ae1b95cad57.mockapi.io/products`,
        product
      );
    },
    updateProductInApi({ commit, state }, product) {
      return axios.put(
        `https://64839515f2e76ae1b95cad57.mockapi.io/products/${product.id}`,
        product
      );
    },
  },
};
