import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

import Home from './components/pages/Home.vue'
import Shop from './components/pages/Shop.vue'
import Login from './components/pages/Login.vue'
import SignUp from './components/pages/SignUp.vue'
import Author from './components/pages/Author.vue'
import Admin from './components/pages/Admin.vue'
import SeeMore from './components/pages/SeeMore.vue'
import Cart from './components/pages/Cart.vue'
import Orders from './components/pages/Orders.vue'
import InsertUpdate from './components/pages/InsertUpdate.vue'
import TextField from './components/form/TextField.vue'
import store from './store/store.js'
import RoleDirective from "./directives/RoleDirective.js"
import {  ValidationObserver,ValidationProvider, extend, localize} from 'vee-validate';
import * as rules from 'vee-validate/dist/rules';
import axios from 'axios'

Object.keys(rules).forEach(key => {
  extend(key ,  rules[key])
});

const instance = axios.create({
  baseURL: "https://64839515f2e76ae1b95cad57.mockapi.io/"
})

Vue.prototype.$axios = instance


const routes = [
  {path:'/' , component: Home},
  {path:'/shop' , component: Shop},
  {path:'/cart' , component: Cart},
  {path:'/orders' , component: Orders},
  {path:'/login' , component: Login},
  {path:'/signUp' , component: SignUp},
  {path:'/author' , component: Author},
  {path:'/admin' , component: Admin},
  {path:'/seeMore/:id' , component: SeeMore},
  {path:'/insertUpdate/:id?' , component: InsertUpdate},
]

const router = new VueRouter({
  routes
}) 

// Vue.component()
Vue.component("TextField" , TextField);
Vue.component('ValidationProvider', ValidationProvider);
Vue.component('ValidationObserver', ValidationObserver);
Vue.directive("role", RoleDirective)

Vue.use(VueRouter)
Vue.config.productionTip = false

new Vue({
  router,
  store,
  created(){
    let user = localStorage.getItem('user')

    if(!user){
      this.$store.commit('changeLoggedUser' , false)
    }

    this.$store.commit('changeLoggedUser' , JSON.parse(user))

    // SET ITEM IN CART
    let itemsInCart = localStorage.getItem('itemsInCart')
    if(itemsInCart && itemsInCart.length){
      this.$store.commit('setProductsInCart' , JSON.parse(itemsInCart))
    }


    // add user admin to users
    let admin = [{
      username: "admin123",
      email: "admin@gmail.com",
      password: "admin123@A",
      fName: "Pera", 
      lName: "Peric",
      role: "admin",
      orders:[]
    }]
    this.$store.commit('addUsers' , admin)

  },
  render: h => h(App),
}).$mount('#app')
