<template>
  <article :class="isOnDiscaunt" :data-id="product.id">
    <span class="discaunt" v-if="product.discaunt">{{ product.discaunt }}%</span>
    <img :src="makeImageSrc" :alt="product.name"/>
    <h4>{{ product.name }}</h4>
    <h5 v-if="!product.discaunt">${{ product.price }}</h5>
    <h5 v-if="product.discaunt"><del>${{ product.price }}</del>
     ${{ getNewPriceWithDiscaunt}}</h5>
    <button v-role="['user']" class="addToCartBtn" @click="addToCart(product.id)">Add to cart</button>
    <button @click="seeMore(product.id)" class="seeMoreBtn">See more</button>
  </article>
</template>
<script>
export default {
  name:"ProductComp",
  props:{
    product:{
      type:Object,
      require: true
    }
  },
  methods:{
    seeMore(id){
      this.$router.push('/seeMore/' + id)
    },
    addToCart(id){
      this.$emit('addProductToCart', id)
    }
  },
  computed:{
    getNewPriceWithDiscaunt(){
      let newPrice = this.product.price * (1 - this.product.discaunt / 100);
      if(Number.isInteger(newPrice)){
        return newPrice;
      }
      return newPrice.toFixed(2);
    },
    makeImageSrc(){
      if(this.product.image.length > 25){
        return this.product.image
      }
      return require('@/assets/img/shop/' + this.product.image + '.png')
    },
    isOnDiscaunt(){
      return this.product.discaunt? 'onDiscaunt': '';
    }
  },
}
</script>