<template>
  <main>
    <div class="insertUpdate">
      <ValidationObserver v-slot="{ invalid, validated }" ref="observer">
        <form  class="formLayout" method="POST" @submit.prevent="handleSubmit">
          <h3 v-if="update">Update product form</h3>
          <h3 v-if="!update">Insert product form</h3>
          <hr class="line"/>
          <ValidationProvider name="manufacturer" :rules="{regex:/^[A-z0-9'@!$#-.,\s\w\d]{2,35}$/}" v-slot="{errors}">
            <TextField :update="update" :error="errors[0]" type="text" name="tbManufacturer" label="Manufacturer" v-model="product.manufacturer"/>
          </ValidationProvider>
          <ValidationProvider name="model" :rules="{regex:/^[A-z0-9'@!$#-.,\s\w\d]{2,35}$/}" v-slot="{errors}">
            <TextField :update="update" :error="errors[0]" type="text" name="tbModel" label="Model" v-model="product.model"/>
          </ValidationProvider>
          <ValidationProvider name="price" :rules="{ min_value: 100 }"  v-slot="{errors}">
            <TextField :update="update" :error="errors[0]" type="text" name="tbPrice" label="Price" v-model="product.price"/>
          </ValidationProvider>
          <ValidationProvider name="discaunt" :rules="{ min_value: 0 }"  v-slot="{errors}">
            <TextField :update="update" :error="errors[0]" type="text" name="tbDiscaunt" label="Discaunt" v-model="product.discaunt"/>
          </ValidationProvider>
          <ValidationProvider name="image" rules="required"  v-slot="{errors}">
            <TextField :update="update" :error="errors[0]" type="text" name="tbImage" label="Image" v-model="product.image"/>
          </ValidationProvider>
          <ValidationProvider name="RAM" rules="required"  v-slot="{errors}">
            <TextField :update="update" :error="errors[0]" type="text" name="tbRAM" label="RAM" v-model="product.specification.RAM"/>
          </ValidationProvider>
          <ValidationProvider name="Batery" rules="required"  v-slot="{errors}">
            <TextField :update="update" :error="errors[0]" type="text" name="tbBatery" label="Batery" v-model="product.specification.Batery"/>
          </ValidationProvider>
          <ValidationProvider name="Screen" rules="required"  v-slot="{errors}">
            <TextField :update="update" :error="errors[0]" type="text" name="tbScreen" label="Screen" v-model="product.specification.Screen"/>
          </ValidationProvider>
          <ValidationProvider name="Camera" rules="required"  v-slot="{errors}">
            <TextField :update="update" :error="errors[0]" type="text" name="tbCamera" label="Camera" v-model="product.specification.Camera"/>
          </ValidationProvider>
          <div class="input-field">
            <input v-if="!update" type="submit" :disabled="invalid || !validated" name="addProduct" id="addProduct" value="Insert"/>
            <input v-if="update" type="submit" :disabled="invalid || !validated" name="updateProduct" id="updateProduct" value="Update"/>
          </div>
          <span v-if="successMess" class="alert alert-succes">{{ successMess }}</span>
          <span v-if="errorMess" class="alert alert-danger">{{ errorMess }}</span>
        </form>
      </ValidationObserver>
    </div>
  </main>
</template>
<script>
import { mapActions } from 'vuex'

export default {
  name: 'InsertUpdateComp',
  data(){
    return{
      product: {
        model: "",
        image: "",
        price: 0,
        discaunt: 0,
        manufacturer: "",
        specification: {
          RAM: "",
          Batery: "",
          Screen: "",
          Camera: ""
        },
        name: "",
      },
      update:false,
      successMess: "",
      errorMess: "",
    }
  },
  mounted(){
    let id = this.$route.params.id
    if(id){
      this.update = true
      this.loadProductWithSomeId(id)
    }
  },
  methods:{
    ...mapActions(['loadProductWithSomeId', 'createNewProductInApi', 'updateProductInApi']),
    loadProductWithSomeId(id){

      this.$store.dispatch('loadProductWithSomeId' , id).then(resp=>{
        this.product = resp.data
        }).catch(err=>{
          this.$emit('showPopup' , 'There is no product with that id');
          this.$router.push('/shop')
        })
    },
    handleSubmit() {
      this.$refs.observer.validate().then(result => {
        if (result) {
          let product = this.product

          product.price = +product.price
          product.discaunt = +product.discaunt
          product.specification = this.product.specification
          product.name = product.manufacturer + ' ' + product.model;
          
          let func, method = ''
          if(this.update){
            func = 'updateProductInApi';
          }else{
            func = 'createNewProductInApi';
          }
          
          this.$store.dispatch(func , product).then(resp=>{
              if(this.update){
                this.$emit('showPopup', 'Product is updated successfully')
              }else{
                this.$emit('showPopup', 'Product is created successfully')
              }
              this.$router.push('/admin')
            }).catch(err=>{
              this.$emit('showPopup', 'Some error occurred')
              this.$router.push('/admin')
            });

        } else {
          this.errorMess = "Some input fields are not valid" 
        }
      });
    },
  }
}
</script>