<template>
  <section class="products">
    <Product @addProductToCart="addProductToCart" v-for="product , ind  in products" :key="ind" :product="product"/>
  </section>
</template>
<script>
import Product from './Product'

export default {
  name:"ProductsSectionComp",
  props:{
    products:{
      type: Array,
      required: true,
    }
  },
  components:{
    Product
  },
  methods:{
    addProductToCart(id){
      this.$emit('addProductToCart', id)
    }
  }
}
</script>
