<template>
  <main>
    <div class="signUp">
      <ValidationObserver v-slot="{ invalid, validated }" ref="observer">
        <form  class="formLayout" method="POST" @submit.prevent="handleSubmit">
          <h3>Sign Up form</h3>
          <hr class="line"/>
          <ValidationProvider name="fName" :rules="{regex:/^[A-Z][a-z]{2,15}$/}" v-slot="{errors}">
            <TextField :error="errors[0]" type="text" name="tbFname" label="First Name" v-model="fName"/>
          </ValidationProvider>
          <ValidationProvider name="lName" :rules="{regex:/^[A-Z][a-z]{2,15}$/}" v-slot="{errors}">
            <TextField :error="errors[0]" type="text" name="tbLname" label="Last Name" v-model="lName"/>
          </ValidationProvider>
          <ValidationProvider name="email" rules="required|email" v-slot="{errors}">
            <TextField :error="errors[0]" type="text" name="tbEmail" label="Email" v-model="email"/>
          </ValidationProvider>
          <ValidationProvider name="username" :rules="{regex:/^[a-zA-Z0-9_-]{3,16}$/}" v-slot="{errors}">
            <TextField :error="errors[0]" type="text" name="tbUsername" label="Username" v-model="username"/>
          </ValidationProvider>
          <ValidationProvider name="password" :rules="{regex:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,24}$/}" v-slot="{errors}">
            <TextField :error="errors[0]" type="password" name="tbPassword" label="Password" v-model="password"/>
          </ValidationProvider>
          <div class="input-field">
            <input type="submit" :disabled="invalid || !validated" name="signUpBtn" id="signUpBtn" value="Sign up"/>
          </div>
          <span v-if="createdMess" class="alert alert-success p-3"> {{createdMess}} </span>
          <span v-if="unableToCreate" class="alert alert-danger p-3">{{ unableToCreate }}</span>
        </form>
      </ValidationObserver>
    </div>
  </main>
</template>
<script>
import { mapActions , mapGetters } from 'vuex'

export default {
  name: 'SignUpComp',
  data(){
    return{
      email:'',
      password: "",
      fName: "",
      lName: "",
      username: "",
      orders: [],
      createdMess: '',
      unableToCreate: ''
    }
  },
  computed : {
    ...mapGetters(['role'])
  },
  mounted(){
    if(this.role != 'unauthorized') {
      this.$router.push("/shop")
    }
  },
  methods:{
    ...mapActions('users' , ['registerUser']),
    handleSubmit() {
      this.$refs.observer.validate().then(result => {
        if (result) {
          
          let user = {
            email: this.email,
            password: this.password,
            fName: this.fName,
            lName: this.lName,
            username: this.username,
            role: 'user',
            orders: this.orders
          }
          this.$store.dispatch('registerUser' , user).then((res)=>{
            this.createdMess = res;
            this.unableToCreate = ''
          }).catch((err)=>{
            this.unableToCreate = err;
            this.createdMess = ''
          });

          // this.created = true;          
        } else {
          
          this.unableToCreate = "There is some invalid input fields";
        }
      });
    },
  }
}
</script>