<template>
  <header>
    <div class="row wrapper">
      <div class="col col-lg-4 col-md-4 col-sm-4 col-12 logo">
        <router-link to="/"><img src="../../assets/img/logo2.png" alt="Logo"></router-link>
      </div>
      <div class="col col-lg-8 col-md-8 col-sm-8 col-12 navigation">
        <Nav :navItems="navItems" :router="true"/>
        <button v-role="['admin', 'user']" class="logout" @click="logout">Logout</button>
      </div>

    </div>
  </header>
</template>
<script>
import Nav from './Nav.vue'

export default {
  name: 'HeaderComponent',
  props:{
    navItems:{
      type: Array,
      required: true
    }
  },
  components:{
    Nav
  },
  methods:{
    logout(){
      // localStorage.removeItem('user');

        localStorage.removeItem('itemsInCart');
        this.$store.commit("changeLoggedUser", false)
        this.$forceUpdate()
        this.$router.push("/shop")
        location.reload()
    }
  }
}
</script>