<template>
  <main>
    <div class="wrapper admin">
      <h2>Admin page</h2>
      <hr class="line"/>
      <router-link to="/insertUpdate">Insert new product</router-link>
      <table class="tableLayout">
        <thead>
          <tr>
            <td>#</td>
            <td>Image</td>
            <td>Manufacturer</td>
            <td>Model</td>
            <td>Price</td>
            <td>Edit</td>
            <td>Remove</td>
          </tr>
        </thead>
        <tbody>
          <ProductInAdminPage @removeProduct="removeProduct" v-for="product, ind in products" :key="ind"  :product="product" :ind="ind"/>
        </tbody>
      </table>
    </div>
  </main>
</template>
<script>
import ProductInAdminPage from './partials/admin/ProductInAdminPage.vue'
import {  mapGetters , mapActions , mapMutations } from 'vuex'

export default {
  name:"AdminComp",
  components:{
    ProductInAdminPage
  },
  data(){
    return{
      products:[]
    }
  },
  computed : {
    ...mapGetters(['role' , 'getProducts','getDeleteMessage'])
  },
  mounted(){
    if(this.role != 'admin') {
      this.$router.push("/shop")
    }

    this.loadProductsFromApi().then(resp=>{
      this.products = this.getProducts
    }).catch(err=>{
      this.$emit('showPopup' , "Some error occurred")
    })
  },
  methods:{
    ...mapActions(['loadProductsFromApi' , 'removeProductsInApi']),
    ...mapMutations(['removeProductFromStore']),
    removeProduct(id){
      this.$store.dispatch('removeProductsInApi', id).then(res=>{
        this.$emit('showPopup', 'Successfuly deleted');
        this.$store.commit('removeProductFromStore' , id)
        this.products = this.getProducts
      }).catch(err=>{
        this.$emit('showPopup', "Some error ocured");
      })
    }
  }
}
</script>