<template>
  <div id="app">
    <Header :navItems="navItems"/>
    <router-view @showPopup="showPopup"></router-view>
    <Footer  :navigationLinks="navItems" :usefullLinks="footerUsefullLinks" :socials="socials"/>
    <Popup @changePopupVisibility="changePopupVisibility" :visible="popupVisibility" :message="popupMessage"/>
    <ToTop @toTop="goToTop" v-if="getScrollTop > 900"/>
  </div>
</template>

<script>
import Header from './components/fixed/Header.vue'
import Footer from './components/fixed/Footer.vue'
import Popup from './components/fixed/Popup.vue'
import ToTop from './components/fixed/ToTop.vue'
import { mapMutations } from 'vuex'

export default {
  name: 'App',
  data(){
    return{
      navItems:[
        {
          path: '/',
          text: 'Home',
          role: ["unauthorized", "user", "admin"]
        },
        {
          path: '/shop',
          text: 'Shop',
          role: ["unauthorized", "user", "admin"]
        },
        {
          path: '/cart',
          text: 'Cart',
          role: ["user"]
        },
        {
          path: '/orders',
          text: 'Orders',
          role: ["user"]
        },
        {
          path: '/author',
          text: 'Author',
          role: ["unauthorized", "user", "admin"]
        },
        {
          path: '/admin',
          text: 'Admin',
          role: ["admin"]
        },
        {
          path: '/login',
          text: 'Login',
          role: ["unauthorized"]
        },
        {
          path: '/signUp',
          text: 'Sign up',
          role: ["unauthorized"]
        },
      ],
      footerUsefullLinks:[
        {
          path:"documentation.pdf",
          text:"Documentation"
        },
        {
          path:"main.js",
          text:"Main.js"
        },
      ],
      socials:[
        {
          path: "https://instagram.com",
          blank:true,
          text:"Instagram"
        },
        {
          path: "https://youtube.com",
          blank:true,
          text:"Youtube"
        },
        {
          path: "https://linkedin.com",
          blank:true,
          text:"LinkedIn"
        },
        {
          path: "https://Twitter.com",
          blank:true,
          text:"Twitter"
        },
      ],
      popupVisibility: false,
      popupMessage: '',
      scrollFromTop: 0
    }
  },
  components:{
    Header,
    Footer,
    Popup,
    ToTop
  },
  mounted(){
    window.addEventListener('scroll', ()=>{
      this.scrollFromTop = window.scrollY
    });

    let usersFromLs = JSON.parse(localStorage.getItem("users"));
    if(usersFromLs && usersFromLs.length){
      this.$store.commit('addUsers' , usersFromLs)
    }
  },
  methods:{
    ...mapMutations('users', ['addUsers']),
    changePopupVisibility(bool){
      this.popupVisibility = bool
    },
    showPopup(mess){
      this.popupMessage = mess;
      this.popupVisibility = true
    },
    goToTop(){
      window.scrollTo( 0,0)
    }
  },
  computed:{
    getScrollTop(){
      return this.scrollFromTop
    }
  }
}
</script>

<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  // margin-top: 60px;
}
</style>
