<template>
  <main>
    <div class="cart">
      <div class="wrapper">
        <h2>Cart page</h2>
        <hr class="line">
        <table v-if="products && products.length" class="tableLayout">
        <thead>
          <tr>
            <th>#</th>
            <th>Image</th>
            <th>Name</th>
            <th>Price</th>
            <th>Quantity</th>
            <th>Remove</th>
          </tr>
        </thead>
        <tbody>
          <CartItem v-for="product , ind in products" :key="ind" :product="product" :ind="ind" @changeProductQuantity="callQuantityChangeFromStore" @removeItemFromCart="callFunctionRemoveItemFromStore"/>
        </tbody>
        </table>
        <div v-if="products && products.length" class="totals">
          <h4 class="total">Total price: <span>${{ Number.isInteger(getTotalPriceForItemsInCart) ? getTotalPriceForItemsInCart: getTotalPriceForItemsInCart.toFixed(2) }}</span></h4>
          <h4 class="total">Total quantity: <span>{{ getTotalQuantityForItemsInCart }}</span></h4>
          <button class="buyBtn" @click="buyProductsFromCart()">Buy products from cart</button>
        </div>
        <div class="empty my-5" v-if="!products || !products.length">
          <h3>There is no products in cart</h3>
        </div>
      </div>
    </div>
  </main>
</template>
<script>
import {mapGetters , mapMutations} from 'vuex'
import CartItem from './partials/cart/CartItem.vue'

export default {
  name:"CartComp",
  data(){
    return{
      products: []
    }
  },
  components:{
    CartItem
  },
  computed:{
    ...mapGetters(['role', 'getProductsFromCart', 'getTotalPriceForItemsInCart' , 'getTotalQuantityForItemsInCart']),
  },
  methods:{
    ...mapMutations(['changeProductQuantity' , 'removeItemFromCart' ,'addProductsToLoggedUserOrders', 'setProductsInCart']),
    callQuantityChangeFromStore(sel , id ){
      this.$store.commit('changeProductQuantity' , {sel: sel, id:id})

      this.products = this.getProductsFromCart
      this.$store.commit('updateTotalQuantityAndPrice')
    },
    callFunctionRemoveItemFromStore(id){
      this.$store.commit('removeItemFromCart' , id)

      this.products = this.getProductsFromCart
      this.$store.commit('updateTotalQuantityAndPrice')
    },
    buyProductsFromCart(){
      this.addProductsFromCartToUserOrders(this.products);
    },
    addProductsFromCartToUserOrders(products){
      if(products && products.length > 0){
        // Add products to user orders
        this.$store.commit('addProductsToLoggedUserOrders', {products: JSON.stringify(products)});
        // Delete products from cart in store
        this.$store.commit('setProductsInCart' , []);
        // Remove products in cart from localstorage
        localStorage.removeItem('itemsInCart');

        this.products = []
        this.$emit('showPopup' , 'Your order has been saved');
      }
    }
  },
  mounted(){
    if(this.role !== 'user'){
      this.$router.push('/shop')
    }
  
    this.products = this.getProductsFromCart
    this.$store.commit('updateTotalQuantityAndPrice')
  }
}
</script>