<template>
  <article>
    <header>
      <h4>Order {{ ind + 1}}</h4>
      <p>Order date: {{ order.date }}</p>
    </header>
    <main>
      <div class="order-details">
        <table class="tableLayout"> 
          <thead>
            <th>#</th>
            <th>Image</th>
            <th>Name</th>
            <th>Price</th>
            <th>Quantity</th>
          </thead>
          <tbody>
            <tr v-for="product, prInd in JSON.parse(order.products)" :key="prInd">
              <td>{{ prInd + 1 }}</td>
              <td><img :src="makeImgUrl(product)" :alt="product.name"/></td>
              <td>{{ product.name }}</td>
              <td v-if="!product.discaunt">${{ product.price }}</td>
              <td v-if="product.discaunt">${{ getCurrentPrice(product) }}</td>
              <td>{{ product.quantity }}</td>
            </tr>
          </tbody>
        </table>
        <footer>
          <h5>Total price: ${{totalPrice}}</h5>
        </footer>
      </div>
    </main>
  </article>
</template>
<script>
export default {
  name: "OrderComp",
  data(){
    return{
    }
  },
  props:{
    order:{
      type: Object,
      required: true
    },
    ind:{
      type: Number,
      required:true
    }
  },
  computed:{
    totalPrice(){
      let products = Array.from(JSON.parse(this.order.products));
      let price = 0;
      products.forEach(p=>{
        if(!p.discaunt){
          price+= p.price * p.quantity
          return
        }
        price+= this.getCurrentPrice(p) * p.quantity
      })
      if(Number.isInteger(price)){
        return price
      }
      return price.toFixed(2)
    }
  },
  methods:{
    getCurrentPrice(product){
      let currentPrice=  product.price * (1 - product.discaunt / 100) 
      if(Number.isInteger(currentPrice)){
        return currentPrice;
      }
      return currentPrice.toFixed(2);
    },
    makeImgUrl(product){
      if(product.image.length > 25){
        return product.image
      }
      return require('@/assets/img/shop/' + product.image + '.png')
    }
  }
}
</script>