<template>
  <main>
    <div class="login">
      <ValidationObserver v-slot="{ invalid, validated }" ref="observer">
        <form  class="formLayout" method="POST" @submit.prevent="handleSubmit">
          <h3>Login form</h3>
          <hr class="line"/>
          <ValidationProvider name="email" rules="required|email" v-slot="{errors}">
            <TextField :error="errors[0]" type="text" name="tbEmail" label="Email" v-model="email"/>
          </ValidationProvider>
          <ValidationProvider name="password" :rules="{regex:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,24}$/}" v-slot="{errors}">
            <TextField :error="errors[0]" type="password" name="tbPassword" label="Password" v-model="password"/>
          </ValidationProvider>
          <div class="input-field">
            <input type="submit" :disabled="invalid || !validated" name="loginBtn" id="loginBtn" value="Login"/>
          </div>
          <span v-if="loginMess" class="alert alert-succes">{{ loginMess }}</span>
          <span v-if="unableToLogin" class="alert alert-danger">{{ unableToLogin }}</span>
        </form>
      </ValidationObserver>
    </div>
  </main>
</template>
<script>
import { mapMutations , mapGetters } from 'vuex'

export default {
  name: 'LoginComp',
  data(){
    return{
      email:'',
      password: "",
      loginMess: "",
      unableToLogin: "",
    }
  },
  computed : {
    ...mapGetters(['role'])
  },
  mounted(){
    if(this.role != 'unauthorized') {
      this.$router.push("/shop")
    }
  },
  methods:{
    ...mapMutations('users' , ['changeLoggedUser']),
    handleSubmit() {
      this.$refs.observer.validate().then(result => {
        if (result) {
          let user = {
            email : this.email,
            password: this.password
          }
          this.$store.dispatch('checkUserParams' , user).then(result => {
            let user = {
              role: result.role,
              username: result.username
            }
            localStorage.setItem('user', JSON.stringify(user));
            
            if(result === 'admin'){
              this.$router.push('/admin')
              return;
            }
            this.$router.push('/shop');
            location.reload()
          }).catch(err => {
            this.unableToLogin = err;
          });

        } else {
          this.unableToLogin = "Some input fields are not valid" 
        }
      });
    },
  }
}
</script>